@media only screen and (max-width: 440px) {
  .project--title {
    font-size: 2.5rem !important;
  }
}

.project--title {
  margin-bottom: 8px !important;
  text-align: center;
}

.project-details {
  margin-bottom: 15px;
}

.projects__container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;

  .projects__cards {
    display: flex;
    flex-direction: column;
    flex: 0 0 calc(50% - 20px);
    background-color: #211D34;
    border-radius: 15px;
    padding: 0 30px;
    padding-top: 10px;
    padding-bottom: 10px;

    /* Adjust width based on the number of items per row */
    // margin: 20px;
    /* Adjust margin as needed */
    .button__wrap {
      margin-top: auto;
    }
  }

  .tech__container {
    display: flex;
    // justify-content: space-between;
    flex-wrap: wrap;

    .tech__items {
      display: inline;
      width: auto;
      /* Adjust width based on the number of items per row */
      margin: 5px;
      /* Adjust margin as needed */
      padding: auto;
      padding-left: 0;
      padding-right: 0;
    }
  }

  .tech-badge {
    background-color: #5D5CCB;
    padding: 8px 10px;
    border-radius: 15px;
    text-align: center;
  }
}

@media screen and (max-width: 979px) {
  .projects__container {
    .projects__cards {
      flex: 0 0 100%;
      ;
    }
  }
}

@media only screen and (max-width: 540px) {
  /*Big smartphones [426px -> 750px]*/
  .project--title {
    font-size: 28px !important;
  }
}